// ----- Setup -----
$document-font-family: -apple-system, sans-serif;
$body-background-color: #f5f5f5;

$base-spacing-size: 1vw;

$header-background-color: #fff;
$header-link-color: #333;
$header-link-active-bg: #007acc;
$header-link-active-color: #fff;

$main-link-border-color: #ddd;
$main-link-active-border-color: $main-link-border-color;
$main-link-description-bg: #fff;
$main-link-description-color: $header-link-color;
$main-link-active-description-bg: $header-link-active-bg;
$main-link-active-description-color: $header-link-active-color;

$footer-bg: $header-link-active-bg;
$footer-color: $header-background-color;

$no-js-separator-color: $main-link-border-color;


// ----- Body -----
body {
	background-color: $body-background-color;
	font-family: $document-font-family;
}


// ---- header ----
header {
	background-color: $header-background-color;
	box-shadow: 0 2px 2px rgba(0, 0, 0, .2);
	display: block;
	z-index: 1;

	li {
		color: $header-link-color;
		display: inline-block;

		&:hover,
		&.active {
			background-color: $header-link-active-bg;
			border-radius: 2px;
			color: $header-link-active-color;
		}
	}

	a { color: inherit; }
}

// ---- Content ----
main {
	margin: $base-spacing-size;

	a {
		margin: $base-spacing-size/4;
		border: 1px solid $main-link-border-color;
		border-radius: 2px;
		background-clip: padding-box;
		position: relative;
		overflow: hidden;
		text-decoration: none;

		&:hover {
			border-color: $main-link-active-border-color;
			z-index: 1;
		}

		// Description
		span {
			background-color: $main-link-description-bg;
			bottom: 0;
			color: $main-link-description-color;
			font-size: .75em;
			left: 0;
			display: block;
			padding: $base-spacing-size/2;
			position: absolute;
			right: 0;
		}
	}
}

// ---- Footer ----
footer {
	background-color: $footer-bg;
	color: $footer-color;
	display: none;

	a { color: inherit; }
}

// ---- Responsive ----
// Mobile view
@include phone-only {
	header {
		font-size: .75em;
		padding: $base-spacing-size*2 $base-spacing-size*1.25;

		a { padding: $base-spacing-size $base-spacing-size*2; }
	}
	main {
		li { width:33%;	}
	}
}

// Default View
@include default {
	header {
		padding: $base-spacing-size*.75 $base-spacing-size*1.25;
		a { padding: $base-spacing-size*.75 $base-spacing-size; }
	}
	main {
		li {
			min-width: 120px;
			width: 20%;
		}

		a {
			transition: transform .2s;
			&:hover { transform: scale(1.1); }
		}
	}
}

// ---- Fallbacks / special cases ----
// separation of content if JS is disabled
body:not(.js) main nav:not(:last-child) {
	margin-bottom: $base-spacing-size*2;
	padding-bottom: $base-spacing-size*2;
	border-bottom: 1px dotted $no-js-separator-color;
}
